.gallery {
  max-width: 100%;
  height: 122px;
  margin: 0 auto;
  background-image: url(../images/gallery/pic-mob-1x-min.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 122px;
  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    & {
      background-image: url(../images/gallery/pic-mob-2x-min.png);
    }
  }
  @include mq(mobile) {
    background-size: 364px 122px;
  }
  @include mq(tablet) {
    background-image: url(../images/gallery/pic-tab-1x-min.png);
    height: 264px;
    width: auto;
    background-size: 786px 264px;
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      & {
        background-image: url(../images/gallery/pic-tab-2x-min.png);
      }
    }
  }
  @include mq(desktop) {
    background-image: url(../images/gallery/pic-desk-1x-min.png);
    width: 1280px;
    height: 430px;
    background-size: 1280px 430px;
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      & {
        background-image: url(../images/gallery/pic-desk-2x-min.png);
      }
    }
  }
}
